import request from '@/utils/request'
// 单次问诊相关接口
// 获取待接诊信息
export function getQuestion(params) {
  return request({
    url: `/admin/question/handler`,
    method: 'get',
    params: params
  })
}

export function getList(params) {
  return request({
    url: '/admin/question/users',
    method: 'get',
    params: params
  })
}

export function getDoctorList() {
  return request({
    url: '/admin/question/list/doctors',
    method: 'get'
  })
}

export function getQuestionReplyList() {
  return request({
    url: '/admin/question/reply',
    method: 'get'
  })
}

export function getAllUserQuestionList(params) {
  return request({
    url: '/admin/question/list/users',
    method: 'get',
    params: params
  })
}

export function getQuestionDetail(params) {
  return request({
    url: `/admin/question/detail/${params}`,
    method: 'get'
  })
}

export function getQuestionHistories(params) {
  return request({
    url: '/admin/question/histories',
    method: 'get',
    params: params
  })
}

export function changeDoctorStatus() {
  return request({
    url: '/admin/question/line',
    method: 'get'
  })
}

export function creatReply(params) {
  return request({
    url: '/admin/question/reply',
    method: 'post',
    data: params
  })
}
// 获取快捷回复列表
export function getReplyList() {
  return request({
    url: '/question_fast_reply/list',
    method: 'get'
  })
}
// 新增快捷回复
export function addReplyApi(params) {
  return request({
    url: '/question_fast_reply',
    method: 'post',
    data: params
  })
}

// 快捷回复删除
export function delReplyApi(id) {
  return request({
    url: `/question_fast_reply/${id}`,
    method: 'delete'
  })
}

// 修改快捷回复
export function updateReplyApi(params) {
  return request({
    url: `/question_fast_reply/${params.id}`,
    method: 'put',
    data: params
  })
}
