<template>
  <div class="search">
    <el-form
      label-position="left"
      label-width="76px"
      :model="formData"
      class="search__form"
    >
      <el-row :gutter="40">
        <el-col :span="5">
          <el-form-item label="问诊状态">
            <el-select
              v-model="formData.status"
              placeholder="请选择问诊状态"
              size="small"
              clearable
            >
              <el-option
                v-for="item in selectOptsMaps.statusList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="接诊人">
            <el-select
              v-model="formData.doctor"
              placeholder="请选择接诊人"
              size="small"
              clearable
            >
              <el-option
                v-for="item in selectOptsMaps.doctorList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="接诊科室">
            <el-select
              v-model="formData.section_office"
              placeholder="请选择接诊科室"
              size="small"
              clearable
            >
              <el-option
                v-for="item in selectOptsMaps.sectionOfficeList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="用户昵称">
            <el-input
              v-model="formData.nickname"
              placeholder="请输入用户昵称"
              size="small"
              clearable
            />
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="40">
        <el-col :span="5">
          <el-form-item label="用户ID">
            <el-input
              v-model="formData.user"
              placeholder="请输入用户ID"
              size="small"
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item label="退款情况">
            <el-select
              v-model="formData.order_status"
              placeholder="请选择退款情况"
              size="small"
              clearable
            >
              <el-option
                v-for="item in selectOptsMaps.orderStatusList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="9">
          <el-form-item label="提问时间">
            <el-date-picker
              v-model="formData.active_at"
              value-format="yyyy-MM-dd HH:mm:ss"
              type="datetimerange"
              start-placeholder="请选择时间范围起始"
              end-placeholder="请选择时间范围结束"
              :default-time="['00:00:00', '23:59:59']"
              size="small"
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <div class="action">
            <div>
              <el-button type="primary" size="small" @click="handleSearch"
                >搜索订单</el-button
              >
              <el-button type="primary" plain size="small" @click="handleReset"
                >重置选项</el-button
              >
            </div>
          </div>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
const defaultformData = {
  status: '',
  active_at: [],
  doctor: '',
  section_office: '',
  nickname: '',
  user: '',
  order_status: ''
}

export default {
  props: {
    localFormData: {
      type: Object,
      default: () => {}
    },
    selectOptsMaps: {
      type: Object,
      required: true,
      default: () => ({
        statusList: [],
        doctorList: [],
        sectionOfficeList: [],
        orderStatusList: []
      })
    }
  },
  data() {
    return {
      formData: {}
    }
  },
  created() {
    this.formData = Object.assign({}, defaultformData, this.localFormData)
  },
  methods: {
    handleSearch() {
      this.$emit('search', { searchParams: this.formData })
    },
    handleReset() {
      this.handleResetFormData()
      this.handleSearch()
    },
    handleResetFormData() {
      this.formData = Object.assign({}, defaultformData)
    }
  }
}
</script>

<style lang="scss">
.search {
  &__form {
    .el-form-item {
      margin-bottom: 20px;
      &__label,
      &__content {
        height: 32px;
        line-height: 32px;
      }
    }
    .el-date-editor,
    .el-select {
      width: 100%;
    }
  }
}
</style>

<style lang="scss" scoped>
.search {
  &__form {
    &__source {
      display: flex;
      align-items: center;
    }
    &__source-scene,
    &__source-custom-value {
      width: 50%;
    }
    &__source-scene {
      padding-right: 14px;
    }
    &__source-custom-value {
      padding-left: 14px;
    }
  }
}
.action {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
</style>
