<template>
  <div>
    <div class="header">
      <div>
        <p class="header__title">待处理问诊</p>
        <div class="card-container">
          <div class="card" @click="goDetail('normal')">
            待接诊
            <div class="card-content">
              <div class="item">
                <div class="name">
                  <i class="iconfont icon-jinri" />
                  待接诊
                </div>
                <div v-if="questionInfo.normal_count > 0" class="count">
                  {{ questionInfo.normal_count }}
                </div>
                <div v-else class="zero">0</div>
              </div>
            </div>
          </div>
          <div class="card" @click="goDetail('reply')">
            追问待回复
            <div class="card-content">
              <div class="item">
                <div class="name">
                  <i class="iconfont icon-jinri" />
                  首次追问
                </div>
                <div v-if="questionInfo.first_ask > 0" class="count">
                  {{ questionInfo.first_ask }}
                </div>
                <div v-else class="zero">0</div>
              </div>
              <div class="item">
                <div class="name">
                  <i class="iconfont icon-quanbu" />
                  二次追问
                </div>
                <div v-if="questionInfo.second_ask > 0" class="count">
                  {{ questionInfo.second_ask }}
                </div>
                <div v-else class="zero">0</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="action">
        <p class="action__text">
          当前问诊状态：{{
            userInfo.question_online_status === 'online'
              ? '在线可接新问诊'
              : '下线不接新问诊'
          }}
        </p>
        <el-button
          type="primary"
          style="width: 170px;"
          @click="handleChangeStaus"
        >
          {{
            userInfo.question_online_status === 'online'
              ? '点击下线'
              : '点击上线'
          }}
        </el-button>
      </div>
    </div>
    <div class="search-from">
      <SearchFrom
        ref="search-form"
        :local-form-data="{}"
        :select-opts-maps="{
          sectionOfficeList,
          statusList,
          orderStatusList,
          doctorList
        }"
        @search="handleSearch"
      />
    </div>
    <div class="table__title">
      用户问诊列表
    </div>
    <el-table v-loading="loading" :data="tableData" border height="700px">
      <el-table-column label="订单号" align="center">
        <template slot-scope="scope">
          <p>{{ scope.row.order_id.order_id }}</p>
        </template>
      </el-table-column>
      <el-table-column label="用户Id" align="center">
        <template slot-scope="scope">
          <div v-if="scope.row.user" class="avatar">
            <p>{{ scope.row.user.user_id }}</p>
          </div>
          <div v-else>
            空
          </div>
        </template>
      </el-table-column>
      <el-table-column label="用户" align="center">
        <template slot-scope="scope">
          <div v-if="scope.row.user" class="avatar">
            <el-avatar :src="scope.row.user.avatar" />
            <p>{{ scope.row.user.nickname }}</p>
          </div>
          <div v-else>
            空
          </div>
        </template>
      </el-table-column>
      <el-table-column
        label="提问时间"
        align="center"
        prop="first_ask_at"
        width="170"
      >
        <template slot-scope="scope">
          <div>
            {{
              scope.row.first_ask_at ? formatTime(scope.row.first_ask_at) : ''
            }}
          </div>
        </template>
      </el-table-column>
      <el-table-column
        label="科室"
        align="center"
        prop="section_office"
        :formatter="row => sectionOffice(row.section_office, '', true)"
      />
      <el-table-column
        label="接诊人"
        align="center"
        prop="doctor"
        :formatter="row => row.doctor.name"
      />
      <el-table-column align="center" prop="active_at" :formatter="waitTime">
        <template slot="header">
          <p>
            <span>首次提问等待时间</span>
            <el-tooltip
              class="item"
              effect="dark"
              content="00:00-08:00不计入等待时长"
              placement="top-start"
            >
              <i
                class="el-icon-warning"
                style="margin-left: 4px;font-size: 16px;"
              />
            </el-tooltip>
          </p>
        </template>
      </el-table-column>
      <el-table-column
        label="首次回复时间"
        align="center"
        prop="first_reply_at"
        width="170"
      >
        <template slot-scope="scope">
          <div>
            {{
              scope.row.first_reply_at
                ? formatTime(scope.row.first_reply_at)
                : ''
            }}
          </div>
        </template>
      </el-table-column>
      <el-table-column
        label="追问次数"
        align="center"
        prop="append_count"
        :formatter="row => `${row.append_count}/2`"
      />
      <el-table-column
        label="问诊状态"
        align="center"
        prop="status"
        :formatter="row => singleAskStatus(row.status)"
      />
      <el-table-column
        label="退款情况"
        align="center"
        prop="order_status"
        width="100"
        :formatter="row => singleAskOrderStatus(row.order_status)"
      />
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button
            type="text"
            @click="
              goDetail('all', {
                userId: scope.row.user._id,
                questionId: scope.row._id
              })
            "
            >查看详情</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      class="pagination"
      :current-page="page.currentPage"
      :page-sizes="[10, 15, 20]"
      :page-size="page.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="page.counts"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    />
    <el-dialog :title="title" :visible.sync="dialogShow" width="30%" center>
      <div
        style="padding: 0 20px;line-height: 1.5;font-size: 16px;font-weight: 600;"
      >
        {{ text }}
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="changeDoctorStatus">{{
          confirmButtonText
        }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import SearchFrom from './components/search-form'
import {
  getQuestion,
  getList,
  changeDoctorStatus,
  getDoctorList
} from '@/api/consultation'

import { mapActions, mapGetters } from 'vuex'
import {
  sectionOffice,
  singleAskOrderStatus,
  singleAskStatus
} from '@/utils/dictionary'
export default {
  components: { SearchFrom },
  data() {
    return {
      loading: true,
      questionInfo: {
        normal_count: '',
        first_ask: '',
        second_ask: ''
      },
      tableData: [],
      doctorList: [],
      title: '',
      text: '',
      confirmButtonText: '',
      dialogShow: false,
      sectionOfficeList: this.sectionOffice('', 'arr', false),
      statusList: [],
      orderStatusList: [],
      page: {},
      params: {}
    }
  },
  computed: {
    ...mapGetters(['userInfo'])
  },
  mounted() {
    this.init()
    this.statusList = this.singleAskStatus('', 'arr')
    this.orderStatusList = this.singleAskOrderStatus('', 'arr')
  },
  methods: {
    singleAskStatus,
    singleAskOrderStatus,
    sectionOffice,
    ...mapActions({
      getUserInfo: 'user/getInfo'
    }),
    handleSizeChange(e) {
      this.page.pageSize = e
    },
    handleCurrentChange(e) {
      this.getList({ currentPage: e })
    },
    handleSearch({ searchParams }) {
      const params = Object.assign({}, searchParams)
      Object.keys(params).forEach(i => {
        if (!params[i] || !params[i].length) {
          delete params[i]
        }
      })
      if (params.active_at && params.active_at.length) {
        params.active_at = params.active_at.join('|')
      }
      this.params = params
      this.getList({ query: params, refresh: true })
    },
    goDetail(type, info) {
      const { href } = this.$router.resolve({
        name: 'ConsultationDetail',
        query: {
          type,
          questionId: info && info.questionId ? info.questionId : '',
          userId: info && info.userId ? info.userId : ''
        }
      })
      window.open(href, '_blank')
    },
    handleChangeStaus() {
      const status = this.userInfo.question_online_status
      this.title = status === 'online' ? '下线提示' : '上线提示'
      this.text =
        status === 'online'
          ? '下线操作成功后，您在用户端的状态将发生变更，用户将无法购买您的单次问诊，请谨慎考虑后进行操作！'
          : '上线操作成功后，您在用户端的状态将发生变更，用户将可以购买您的单次问诊，请谨慎考虑后进行操作！'
      this.confirmButtonText = status === 'online' ? '确定下线' : '确定上线'
      this.dialogShow = true
    },
    waitTime(row) {
      const { hour, minutes } = this.getHours(
        row.first_ask_at,
        row.first_reply_at || row.closed_at || new Date()
      )
      return `${hour}小时${minutes}分钟`
    },
    getHours(start, end) {
      const startAt = new Date(start)
      startAt.setMinutes(0)
      startAt.setSeconds(0)
      startAt.setMilliseconds(0)
      const endAt = new Date(end)
      endAt.setMinutes(0)
      endAt.setSeconds(0)
      endAt.setMilliseconds(0)
      let hour = 0
      // eslint-disable-next-line no-unmodified-loop-condition
      while (startAt < endAt) {
        const startHour = startAt.getHours()
        if (startHour > 8 && startHour <= 23) {
          hour += 1
        }
        startAt.setTime(startAt.setHours(startAt.getHours() + 1))
      }
      const startTime = new Date(start)
      const endTime = new Date(end)
      const startHour = startTime.getHours()
      const endHour = endTime.getHours()
      let startMinutes = startTime.getMinutes()
      let startSeconds = startTime.getSeconds()
      let startMilliseconds = startTime.getMilliseconds()
      if (startHour <= 7) {
        startMinutes = 0
        startSeconds = 0
        startMilliseconds = 0
      }
      let endMinutes = endTime.getMinutes()
      let endSeconds = endTime.getSeconds()
      let endMilliseconds = endTime.getMilliseconds()
      if (endHour <= 7 && endHour > 0) {
        endMinutes = 0
        endSeconds = 0
        endMilliseconds = 0
      } else if (endHour === 0) {
        endMinutes = 59
        endSeconds = 59
        endMilliseconds = 0
      }
      let minutes = endMinutes - startMinutes
      let seconds = endSeconds - startSeconds
      let milliseconds = endMilliseconds - startMilliseconds
      if (milliseconds < 0) {
        seconds = seconds - 1
        milliseconds = 1000 + milliseconds
      }
      if (seconds < 0) {
        minutes = minutes - 1
        seconds = 60 + seconds
      }
      if (minutes < 0) {
        hour = hour - 1
        minutes = 60 + minutes
      }
      return { hour, minutes, seconds, milliseconds }
    },
    async changeDoctorStatus() {
      try {
        await changeDoctorStatus()
        this.$message({
          type: 'success',
          message: '操作成功'
        })
        this.getUserInfo()
        this.dialogShow = false
      } catch (error) {
        console.log(error)
      }
    },
    async getDoctorList() {
      const { data } = await getDoctorList({ bus_role: 'doctor' })
      this.doctorList = data.list.map(i => ({ label: i.name, value: i._id }))
    },
    async getList({
      query = this.params,
      currentPage = 1,
      refresh = false
    } = {}) {
      const {
        data: { list, ...page }
      } = await getList({
        ...query,
        page: refresh ? 1 : currentPage,
        pageSize: this.page.pageSize || 10
      })
      this.tableData = list
      this.page = page
      this.loading = false
    },
    async init() {
      const { data } = await getQuestion()
      this.questionInfo = data
      this.getList()
      this.getDoctorList()
    }
  }
}
</script>
<style lang="scss" scoped>
.header {
  display: flex;
  justify-content: space-between;
  padding: 0 20px 20px 20px;

  .header__title {
    margin-bottom: 20px;
    font-size: 16px;
    font-weight: 600;
  }
}

.card-container {
  display: flex;
  .card {
    margin-right: 30px;
    padding: 20px;
    width: 260px;
    height: 125px;
    font-size: 16px;
    color: #0085ff;
    border: 2px solid #e8eaec;
    border-radius: 8px;
    cursor: pointer;
    &:hover {
      border: 2px solid rgba(0, 133, 255, 0.15);
      box-shadow: 0 0 4px 2px rgba(0, 133, 255, 0.15);
    }
    .card-content {
      padding: 10px;
      .item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 5px 0;
        font-size: 14px;
        color: #576277;
        .count {
          padding: 2px 4px;
          color: white;
          background: #f0717b;
          border-radius: 5px;
        }
        .zero {
          padding: 2px 4px;
        }
      }
    }
  }
}

.action {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 50px;
  flex-direction: column;

  .action__text {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 20px;
  }
}

.table__title {
  padding: 20px 20px;
  background: #f3f6f9;
}

.search-from {
  padding-bottom: 20px;
}

.pagination {
  margin-top: 20px;
  text-align: center;
}
</style>
